<template>
  <div class="navBar">
    <div class="navBar_left">
    </div>
    <div class="navBar_center">
      <ul>
        <li>
            <img src="../../assets/images/top.png" alt="logo">
        </li>
        <li v-for="item in mes"><a href="#">{{item}}</a></li>
        <li>
          <a href="#" class="iconfont icon-dianhua01">400-696-5558/1817134383</a>
        </li>
      </ul>
    </div>
    <div class="navBar_right">
    </div>
  </div>
</template>

<script>
  export default {
    name: "NavBar",
    data() {
      return {
        mes: [
          '首页', '医院规划', '建筑设计', '室内设计', '医院配备', '运营服务', '关于我们', '联系我们'
        ]
      }
    }
  }
</script>

<style scoped>
  .navBar {
    display: flex;
  }

  .navBar_left {
    flex: 3;
  }

  .navBar_center {
    flex: 20;
  }

  .navBar_center ul {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .navBar_center ul li {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navBar_center ul li a{
    padding-bottom: 4px;
  }
  .navBar_center ul li a:hover{
    color: rgba(48,255,235,0.59);
    border-bottom: 3px solid rgba(48,255,235,0.59);
  }
  .navBar_center ul li:last-child a {
    font-size: 12px;
    white-space: nowrap;
  }

  .navBar_right {
    flex: 3;
  }
</style>



